import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

export default new Router({
  base: __dirname,
  mode: 'hash',
  scrollBehavior: function(to) {
	if (to.hash) {
		return { selector: to.hash }
			} else {
			return { x: 0, y: 0 }
		}
	},
  routes: [
    {
      path: '/',
      name: 'main',
      component: ()=> import('@/page/00_user/main')
    },
    {
      path: '/history',
      name: 'history',
      component: ()=> import('@/page/00_user/history')
    },
    {
      path: "/uiux",
      name: "uiux",
      component: ()=> import('@/page/01_portfolio/uiux')
    },
    {
      path: "/design",
      name: "design",
      component: ()=> import('@/page/01_portfolio/design')
    },
    {
      path: "/plan",
      name: "plan",
      component: ()=> import('@/page/01_portfolio/plan')
    },
    {
      path: "/personal",
      name: "personal",
      component: ()=> import('@/page/02_art/personal')
    },
    {
      path: "/contents",
      name: "contents",
      component: ()=> import('@/components/contents/contents')
    },
    {
      path: "/mainContents",
      name: "mainContents",
      component: ()=> import('@/components/contents/mainContents')
    },
    {
      path: '/test1',
      name: 'test1',
      component: ()=> import('@/page/03_test/test1'),
      children:[
        {
          path: '/t_1',
          name: 't_1',
          component: ()=> import('@/components/test/home')
        },
        {
          path: '/t_2',
          name: 't_2',
          component: ()=> import('@/components/test/forms')
        },
        {
          path: '/t_3',
          name: 't_3',
          component: ()=> import('@/components/test/animations')
        }
      ]
    },
    {
      path: '/firebase',
      name: 'firebase',
      component: ()=> import('@/page/03_test/firebase')
    }
  ]
});