import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './plugins/router'
import store from './plugins/store'
import vuetify from './plugins/vuetify'
import i18n from './plugins/i18n'
import VueToastr2 from 'vue-toastr-2'
import AnimateCss from 'animate.css'
import VueScrollProgressBar from '@guillaumebriday/vue-scroll-progress-bar'

// component
import './components';

// firebase
import './plugins/firebase'

Vue.config.productionTip = false

// toastr2
import 'vue-toastr-2/dist/vue-toastr-2.min.css';
window.toastr = require('toastr');
Vue.use(VueToastr2);

// Animatie.css url-https://github.com/daneden/animate.css
Vue.use(AnimateCss)

// pinch image
import PinchZoom from 'vue-pinch-zoom'
Vue.component('pinch-zoom', PinchZoom)

// 각 components 에서 this.$eventBus.$emit('key', 'value'); 이벤트 발행
// this.$eventBus.$on('key', 'functionName'); 이벤트 구독, created or mounted 때 등록
// this.$eventBus.$off('key'); 이벤트 구독해제, beforeDestory 때 등록
Vue.prototype.$eventBus = new Vue();

// VueScrollProgressBar https://github.com/guillaumebriday/vue-scroll-progress-bar
Vue.use(VueScrollProgressBar)

new Vue({
  vuetify,
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
