<template>
  <v-app-bar app dark :clipped-left="true" fixed color="primary" VueScrollProgress>
    <vue-scroll-progress-bar height="3px" backgroundColor="#5ea4f4"/>
    <v-app-bar-nav-icon v-on:click="$eventBus.$emit('drawerToggle')" class="topbar_logo">
      <img src="@/assets/Logo_W.png" width="20px" height="20px" class="logo" alt="">
    </v-app-bar-nav-icon>
    <v-toolbar-title class="text-uppercase">
      <span class="sub_title">
        {{getTitle}}
      </span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items>
      <template v-for="(item, index) in menu_items">
        <v-btn 
          color="primary"
          class="top_menu"
          depressed
          large 
          :key="index" 
          :to="item.link"
        >{{item.title}}</v-btn>
      </template>
      <v-btn
        icon
        v-on:click=" dialog = true "
        v-if="!getLogin"
        class="ml-3"
      >
        <v-icon>account_circle</v-icon>
      </v-btn>
      <v-btn
        icon
        v-on:click="logout()"
        v-if="getLogin"
        class="ml-3"
      >
        <v-icon>exit_to_app</v-icon>
      </v-btn>
      <!-- login pop_up window -->
      <v-dialog v-model="dialog" persistent max-width="350">        
        <v-card class="pa-3">
          <v-col>
            <v-card-title class="headline">LOGIN</v-card-title>
            <v-col class="pa-4">
              <img :src="require('@/assets/logo.png')" width="160"/>
            </v-col>
            <v-form class="pa-4">
              <v-text-field
                v-model="userId"
                prepend-icon="person"
                data-vv-name="ID"
                :label="$t('basic.lblID')"
                :error-messages="errors.collect('ID')"
                v-validate="'min:2|max:20'"
                type="text"
              ></v-text-field>
              <v-text-field
                v-model="password"
                prepend-icon="account_circle"
                data-vv-name="Password"
                :error-messages="errors.collect('Password')"
                :label="$t('basic.lblPassword')"
                v-validate="'min:2|max:20'"
                type="password"
                v-on:keyup.enter="login()"
              ></v-text-field>
              <!-- <v-select
                v-model="selectLanguage"
                prepend-icon="language"
                :items="language"
                label="주사용 언어"
              ></v-select> -->
            </v-form>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn class="login_btn" color="secondary" text @click="closePop()">{{$t("basic.btn_cancle")}}</v-btn>
              <v-btn class="login_btn" color="primary" text @click="login()">{{$t("basic.btn_ok")}}</v-btn>
            </v-card-actions>
          </v-col>
        </v-card>
      </v-dialog>
      <!-- login pop_up window -->
    </v-toolbar-items>
  </v-app-bar>
</template>

<script>
import {mapActions, mapGetters, mapMutations} from 'vuex';
// import $ from "jquery";

export default {
  data: () => ({
    dialog: false,
    language:['en','ko'],
    selectLanguage:'ko',
    userId:'',
    password: '',
    menu_items:[
      {
        title: 'main',
        link: '/'
      },
      {
        title: 'portfolio',
        link: '/uiux'
      },
      {
        title: 'history',
        link: '/history'
      }
    ]
  }),
  computed:{
    ...mapGetters([
      'getTitle',
      'getLogin',
      'getUsers'
    ])
  },
  methods: {
    ...mapMutations([
      'login_menu',
      'changeLogin'
    ]),
    ...mapActions(['changeLang']),
    closePop(){
      this.dialog = false;
      this.userId = '';
      this.password = '';
      this.$validator.reset();
    },
    logout(){
      var select = confirm("정말로 로그아웃하시겠습니까?");
      if(select){
        this.changeLogin(false)
        this.dialog = false;
        this.login_menu(false)
      }      
    },
    login(){
      // console.log('getUsers',this.getUsers)
      if(this.userId !== '' || this.password !== ''){
        var userFilter = this.getUsers.filter((item)=> item.id == this.userId)
        if(userFilter.length == 0){
          this.$toastr.error( this.$t('basic.ErrorIDMassage'), this.$t('basic.ErrorIDTitle'),{timeOut: 1500});
        }else{
          if(userFilter[0].pw == this.password){
            this.$toastr.success( this.$t('basic.SucessLoginMassage'), userFilter[0].id+this.$t('basic.SucessLoginTitle'),{timeOut: 1500});
            this.changeLang(true)
            this.changeLogin(true)
            this.dialog = false;
          }else{
            this.$toastr.error( this.$t('basic.ErrorPWMassage'), this.$t('basic.ErrorPWTitle'),{timeOut: 1500});
          }
        }
      }
    }
  }
}
</script>

<style>
.progress-bar-container--container {
  top: 61px !important;
}
.progress-bar-container--backgroundColor {
  background-color:red !important;
}
.mainTitle{
  font-size: 1.3rem;
}
.userName{
  font-size: 1.2rem;
}
.sub_title{
  font-size: 1.1rem !important;
  font-weight: 500;
}
.login_btn{
  font-weight: 800;
}
.topbar_logo{
  margin-left: 0.5rem !important;
  margin-right: 0.5rem !important;
}
@media screen and (max-width: 960px) {
  .progress-bar-container--container {
    top: 54px !important;
  }
  .top_menu{
    display: none;
  }
  .topbar_logo{
    margin-left: 0rem !important;
  }
}
</style>
