<template>
	<v-container fluid class="contents ma-0 pa-0">
		<v-col cols="12" align="center" justify="center" class="pa-0">	
			<v-tabs
				v-model="model"
				color="primary"
				center
				show-arrows
				:active-class="scroll()"
				v-show="!smallWidth"
				v-if="!ie"
			>
				<v-tab v-for="item in works" :key="item.title" v-on:bind="selectItem(item)">
					{{item.title}}
				</v-tab>
			</v-tabs>
			<v-col v-show="smallWidth" v-if="!ie" cols="12" sm="12" md="12" lg="11" xl="11" align="center" justify="center">
				<v-sheet class="mx-auto" elevation="0" max-width="100%" color="transparent">
					<v-slide-group v-model="model" class="justify-center" center-active show-arrows mandatory>
						<v-slide-item v-for="(item , index) in works" :key="index" v-slot:default="{active, toggle}">
							<v-card class="ma-4" height="130" width="150" :color="active ? 'primary' : ''" v-on:click="selectItem(item)"  @click="toggle">
								<v-img class="sheet_img" :src="require(`@/assets/portfolio/${item.src}`)"></v-img>
								<v-col cols="12" align="center" justify="center">
									<span :class="active ? 'contents_sub_title_light':'contents_sub_title_dark'">{{item.title}}</span>
								</v-col>
							</v-card>
						</v-slide-item>
					</v-slide-group>
				</v-sheet>
			</v-col>
			<v-col align="center" class="pa-0">
				<v-tabs-items v-model="model" class="works_bg">
					<v-tab-item
						v-for="worksItem in works"
						:key="worksItem.title"
					>
						<v-col cols="12" md="10" lg="8" class="works">
							<v-carousel :height="imgHeight" hide-delimiter-background show-arrows-on-hover>
								<v-carousel-item 
									v-for="(item,i) in worksItem.contents.img" 
									:key="i"
								>
								<v-container fill-height class="pa-0">
									<v-col class="pa-0" align="center">
										<v-img
											:src="require(`@/assets/portfolio/${item}`)"
											style="width:100%;"
											@click="onFullScreen(item)"
										/>
									</v-col>
								</v-container>
								</v-carousel-item>
							</v-carousel>
						</v-col>
						<v-col cols="11" md="10" lg="8" align="center" class="pa-5">
							<v-row>
								<v-col cols="12" md="9">
									<p class="contents_title">{{ worksItem.title }}</p>
									<p class="contents_text" v-html="worksItem.contents.text"></p>
								</v-col>
								<v-col cols="12" md="3" class="contents_date" align-self="end">
									<p>{{ worksItem.contents.ability }}</p>
									<p>{{ worksItem.contents.period }}</p>
								</v-col>
							</v-row>
							<v-row class="pt-3">
								<v-btn 
									:v-if="worksItem.contents.sub" 
									v-for="(sub, index) in worksItem.contents.sub" 
									:key="index"
									:href="sub.to"
									target="_blank"
									class="contents_sub_btn ml-2"
									>
									{{sub.title}}
								</v-btn>
							</v-row>
						</v-col>
					</v-tab-item>
				</v-tabs-items>
			</v-col>
		</v-col>
		<dialog-full-screen
			:dialog="fullScreenDialog"
            :image="fullScreenDialogImage"
            v-on:close-dialog="fullScreenDialog = false"
		/>
	</v-container>
</template>

<script>
// import $ from "jquery";
export default {
	data: () => ({
		model:0,
		smallWidth: false,
		ie: false,
		fullScreenDialog: false,
        fullScreenDialogImage: null,
	}),
	computed:{
		works(){
			return this.$store.getters.getWorks;
		},
		workIndex(){
			return this.$store.getters.getWorksSelect;
		},
		imgHeight(){
			let height = 180
			let widowWidth = window.innerWidth
            if(widowWidth > 321){
                height = 210
            }
            if(widowWidth > 400){
				height = 230
            }
            if(widowWidth > 425){
				height = 430
            }
            if(widowWidth > 1439){
				height = 550
            }
            if(widowWidth > 1600){
				height = 580
            }
            if(widowWidth > 1081){
				height = 700
            }
			return height
		},
	},
	created() {
		window.addEventListener('beforeunload', this.handler)
		window.addEventListener('resize', this.handleResize)
		if(this.workIndex == null){
			this.$router.push('/uiux');
		}
		this.ieCheck();
		this.model = this.workIndex;
		this.handleResize();
	},
	mounted(){
		this.$store.dispatch('changeTitle',this.works[this.model].title)
	},
	methods:{
		onFullScreen(item){
            this.fullScreenDialog = true
            this.fullScreenDialogImage = item
        },
		ieCheck(){
			// ie check
			var agent = navigator.userAgent.toLowerCase();
			if ( (navigator.appName == 'Netscape' && agent.indexOf('trident') != -1) || (agent.indexOf("msie") != -1)) {
				// console.log("ie");
				this.ie = true;
			}else{
				// console.log("ieNO");
				this.ie = false;
			}
		},
		scroll(){
			// 스크롤 최상단으로 올려주기
			window.scrollTo(0,0);
		},
		handler() {
			// 새로고침감지 event
			// event.returnValue = 'Write something'
			alert(this.$refs.message.value)
		},
		handleResize(){
			var width = window.innerWidth;
			if( width > 1264 ){
				this.smallWidth = true;
			}if( width < 600 ){
				this.smallWidth = false;
			}
		},
		selectItem(item){
			this.$store.dispatch('changeTitle',item.title);
		}
	},
	watch:{
		model(value){
			this.$store.dispatch('changeTitle',this.works[value].title);
		}
	},
	updated() {
		this.$nextTick(function () {
		});
	},
	destroyed() {
		window.removeEventListener('resize', this.handleResize)
	}
}
</script>

<style>
.theme--dark.v-btn.v-btn--icon{
    color: #192E49 !important;
    opacity: 1;
}
.works_carousel{
	width:100%;
	height:100% !important;
}
.v-tabs-bar.v-slide-group--is-overflowing.v-tabs-bar--is-mobile:not(.v-tabs-bar--show-arrows):not(.v-slide-group--has-affixes) .v-slide-group__prev {
    display: none;
    visibility: hidden;
}
/*=== v slide center ===*/
.v-slide-group__content{
	-webkit-box-pack: center!important;
    -ms-flex-pack: center!important;
    justify-content: center!important;
}
.works_bg{
	background: transparent !important;
}
.works{
	padding: 2rem 0 !important;
}
.contents_title{
	text-align: left;
	font-size: 1.5rem;
	font-weight: 600;
	color: #143b83;
}
.contents_text{
	text-align: left;
	font-size: 1.1rem;
	color: #414141;
}
.contents_date{
	font-size: 0.9rem;
	font-weight: 550;
	color: #414141;
	height: 100%;
	text-align: right;
}
.contents_sub_title_light{
	vertical-align: center;
	font-size: 0.9rem;
	font-weight: 700;
	color: #fff;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* 라인수 */
	-webkit-box-orient: vertical;
	word-break: keep-all;
	word-wrap:break-word; 
}
.contents_sub_title_dark{
	vertical-align: center;
	font-size: 0.9rem;
	font-weight: 700;
	color: #192E49;
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 1; /* 라인수 */
	-webkit-box-orient: vertical;
	word-break: keep-all;
	word-wrap:break-word; 
}
.contents_sub_btn{
	font-size: 1rem !important;
}
.sheet_img{
	overflow: hidden;
	height: 85px !important;
}
@media screen and (max-width: 600px) {
	.v-carousel__controls__item{
		margin: 0 0.1rem !important;
	}
	.works .v-icon{
		font-size: 0.8rem !important;
	}
}
@media screen and (max-width: 960px) {
	.contents{
		padding-top: 0;
	}
	.works{
		margin:0 !important;
		padding: 0 !important;
	}
}
@media screen and (max-width: 768px) {
	.contents_title{
		font-size: 1.2rem;
	}
	.contents_text{
		font-size: 0.9rem;
	}
	.contents_date{
		font-size: 0.75rem;
	}
	.contents_sub_btn{
		font-size: 0.75rem !important;
	}
}
</style>