import Vue  from 'vue';
import Vuex from 'vuex';

// firebase
import './firebase'

var i18n = require('./i18n').default;
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLogin: false,
    users:[],
    menus: i18n.t("menu"),
    main: i18n.t("main"),
    mainSelect: null,
    history: i18n.t("history"),
    portfolio:{
      works:[],
      worksSelect: null,
      uiux: i18n.t("uiux"),
      design: i18n.t("design"),
      plan: i18n.t("plan"),
      personal: i18n.t("personal"),      
    },
    ui: {
      subject:'uiux',
      title:''
    }
  },
  actions: {
    // stats 항목을 바꿀 경우 actions에서 수정 후 mutations로 commit한다.
    changeLang({commit},data){
      commit('changeLang',data);
      commit('changeWorks');
    },
    changeTitle({commit},data){
      commit('changeTitle',data);
    },
    changeSubject({commit},data){
      commit('changeSubject',data);
    },
    changeWorks({commit}){
      commit('changeWorks');
    },
    changeMain({commit},data){
      commit('changeMain',data);
    },
    worksSelect({commit},data){
      commit('worksSelect',data);
    }
  },
  getters: {
    getTitle: function(state){
      return state.ui.title;
    },
    getSubject: function(state){
      return state.ui.subject;
    },
    getLogin: function(state){
      return state.isLogin;
    },
    getMenus: function(state){
      return state.menus;
    },
    getWorks: function(state){
      return state.portfolio.works;
    },
    getWorksSelect: function(state){
      return state.portfolio.worksSelect;
    },
    getUIUX: function(state){
      return state.portfolio.uiux;
    },
    getDesign: function(state){
      return state.portfolio.design;
    },
    getPlan: function(state){
      return state.portfolio.plan;
    },
    getMain: function(state){
      return state.main;
    },
    getMainSelect: function(state){
      return state.mainSelect;
    },
    getHistory: function(state){
      return state.history;
    },
    getUsers: function(state){
      return state.users;
    }
  },
  mutations: {
    changeUsers(state, users){
      let userItems = [
        { id: 'chok0908', password: 'wjrxhak98'},
        { id: 'test',     password: '123456'}
      ]
      if(users.length > 0) userItems = users
      state.users = userItems
    },
    changeLogin(state, data){
      state.isLogin = data
    },
    changeTitle(state, data){
      state.ui.title = data;
    },
    changeSubject(state, data){
      state.ui.subject = data;
    },
    changeLang(state, data){
      if(data == true){
        state.menus = i18n.t("menu_login");
      }else{
        state.menus = i18n.t("menu");
      }
      state.history = i18n.t("history");
      state.main    = i18n.t("main");
    },
    changeWorks(state){
      if(state.ui.subject == 'uiux')          { state.portfolio.uiux    = i18n.t("uiux");       state.portfolio.works = state.portfolio.uiux;}
      if(state.ui.subject == 'design')        { state.portfolio.design  = i18n.t("design");     state.portfolio.works = state.portfolio.design;}
      if(state.ui.subject == 'plan')          { state.portfolio.plan    = i18n.t("plan");       state.portfolio.works = state.portfolio.plan;}
      if(state.ui.subject == 'personal')      { state.portfolio.personal= i18n.t("personal");   state.portfolio.works = state.portfolio.personal;}
    },
    changeMain(state,data){
      state.mainSelect = data;
    },
    worksSelect(state, data){
      state.portfolio.worksSelect = data;
    }
  }
});