<template>
	<v-container fluid class="pa-0 ma-0">
		<v-col cols="12" align="start" justify="center" class="pa-0 ma-0">
			<v-tabs-items v-model="tab">
				<v-tab-item
					v-for="i in tabs"
					:key="i.title"
				>
				<v-img :src="require(`@/assets/portfolio/${i.sub_img}`)" class="mainContents_img">
					<!-- <v-col cols="12" align="start" class="mainContents_item" :class="i.color">
						<p class="main_title" color="primary">{{i.title}}</p>
						<p class="main_sub">{{i.sub}}</p>
						<img :src="require(`@/assets/portfolio/${i.icon}`)" class="icon_img">
					</v-col> -->
				</v-img>
				<v-card flat>
					<v-col 
						cols="12"
						v-for="content in i.contents" 
						:key="content.title" 
						align="center" 
						justify="start" 
						class="pa-0 ma-0"
					>
						<v-col cols="12" md="10" lg="7" align="start"  class="mt-10">
							<v-card-text v-if="content.title !== 'empty'" class="maincontents_title" color="primary">
								{{content.title}}
							</v-card-text>
							<v-card-text v-if="content.text !== 'empty'" class="maincontents_text">
								{{content.text}}
							</v-card-text>
						</v-col>
						<v-img
							v-if="content.src !== 'empty'"
							class="mainContents_img" 
							:src="require(`@/assets/portfolio/${content.src}`)">
						</v-img>
					</v-col>
				</v-card>
				</v-tab-item>
			</v-tabs-items>
			<v-tabs
				v-model="tab"
				color="primary"
				center
				:active-class="scroll()"
			>
				<v-tab v-for="item in tabs" :key="item.title">
					{{item.title}}
				</v-tab>
			</v-tabs>
		</v-col>
	</v-container>
</template>

<script>
import $ from "jquery";
export default {
	data: () => ({
		tab: null,
		smallWidth: false
	}),
	computed:{
		tabs(){
			return this.$store.getters.getMain;
		}
	},
	created() {
		if(this.$store.getters.getMainSelect == null){
			this.$router.push('/').catch(err => err);
		}
		this.tab = this.$store.getters.getMainSelect;
		window.addEventListener('resize', this.handleResize)
		this.handleResize();
  },
	mounted(){
		if(this.smallWidth == true){
			this.$eventBus.$emit('drawerHome', false);
		}		
	},
	methods:{
		handleResize(){
			var width = window.innerWidth;
			if( width > 1264 ){
				this.smallWidth = true;
			}if( width < 600 ){
				this.smallWidth = false;
			}
		},
		selectItem(item, index){
			this.worksItem = item;
			window.scrollTo(0,0);
		},
		scroll(){
			// 스크롤 최상단으로 올려주기
			window.scrollTo(0,0);
		}
	},
	watch:{
		tab(value){
			this.$store.dispatch('changeTitle',this.tabs[value].title);
		}
	},
	updated() {
		this.$nextTick(function () {
		});
	},
	destroyed() {
		window.removeEventListener('resize', this.handleResize);
	},
	beforeDestroy(){
		this.$eventBus.$emit('drawerHome', true);
	}
}
</script>

<style>
.mainContents_item{
	padding: 6rem 10rem;
}
.mainContents_img{
	width: 100%;
}
.maincontents_title{
	width: 100%;
	padding: 2rem;
	text-align: left;
	font-size: 2rem;
	line-height: 2.2rem;
	text-transform: uppercase;
}
.maincontents_text{
	width: 100%;
	padding: 2rem;
	text-align: left;
	line-height: 1.5rem;
	font-size: 1.2rem;
}
.project_title{
	font-size: 2rem;
}
.contents_icon_img{
	width: 80px;
}
.contents_item{
	padding: 3rem;
	vertical-align: end;
}
@media screen and (max-width: 1200px) {
	.mainContents_item{
		padding: 3rem 5rem;
	}
	.maincontents_title{
		padding: 1.8rem;
		padding-bottom: 0rem;
		font-size: 1.5rem;
	}
	.maincontents_text{
		padding: 1.8rem;
		font-size: 1rem;
	}
}
@media screen and (max-width: 960px) {
	.mainContents_item{
		padding: 3rem;
	}
}
@media screen and (max-width: 768px) {
	.maincontents_title{
		padding: 1rem;
		padding-bottom: 0rem;
		font-size: 1.2rem;
	}
	.maincontents_text{
		padding: 1.2rem;
		font-size: 0.8rem;
	}
}
</style>