<template>
    <v-dialog
        v-model="dialog"
        width="100%"
        fullscreen
    >
        <v-card class="fill-height">
            <v-container fill-height class="pa-0">
                <v-col
                    cols="12"
                    align-self="center"
                    class="pa-0"
                >
                    <pinch-zoom
                        ref="pinchImage"
                        :wheel="true"
                        :autoHeight="true"
                        :overflow="'visible'"
                    >
                        <v-img
                            :src="require(`@/assets/portfolio/${image}`)"
                            @click="closeDialog(this)"
                        />
                    </pinch-zoom>
                </v-col>
            </v-container>
            <v-btn
                fab
                small
                elevation="0"
                @click="closeDialog(this)"
                class="close_button"
            >
                <v-icon>mdi-close</v-icon>
            </v-btn>
        </v-card>
    </v-dialog>
</template>

<script>
export default {
    name: 'FullScreenDialog',
    props:['dialog', 'image'],
    data () {
        return {
        }
    },
    methods: {
        closeDialog (e) {
            // console.log(this.$refs.pinchImage)
            // zoom reset
            const pinch = this.$refs.pinchImage
            if(pinch.ivyPinch.scale !== 1) pinch.toggleZoom()
            
            this.$emit('close-dialog',e)
        }
    },
}
</script>

<style scoped lang="scss">
    .close_button{
        position: fixed;
        top: 1rem;
        right: 1rem;
    }
</style>
