<template>
  <v-app>
    <core-Drawer/>
    <core-Topbar/>
    <v-main>
      <router-view/>
    </v-main>
    <core-Footer/>
  </v-app>
</template>

<script>
import { mapMutations } from 'vuex'

export default {
  components: {
  },
  data () {
    return {
    }
  },
  computed: {
  },
  mounted(){
    this.getFirebaseUsers()
  },
  methods:{
    ...mapMutations(['changeUsers']),
    getFirebaseUsers(){
      this.$firebase.database().ref('site/user/').on('value', (sn) => {
        // value 값만 가져오기
        const value = Object.values(sn.val())
        // key 값만 가져오기
        // const key = Object.keys(sn.val())

        // key value 가져오기
        // let keyValue = sn.val()
        
        this.changeUsers(value)
      })
    }
  }
}
</script>
<style>
#app {
  font-family: 'Noto Sans Korean', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  font-size: 12px;
  word-break: keep-all;
}
::-webkit-scrollbar { 
  display:none;
}
@media screen and (max-width: 600px) {
 .xsHide{
   display: none;
 }
}
</style>
